(function() {
  'use strict';

  angular
    .module('front')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('front')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('front')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  // 'use strict';

  angular
    .module('front')
    .service('GatewayService', GatewayService);

  GatewayService.$inject = ['$log', '$q', '$cookies', '$http'];
  /** @ngInject */
  function GatewayService($log, $q, $cookies, $http) {
    var baseUrl = "http://localhost:4010"
    return {
      login: function(data) {
        var promise = $q.defer();
        var baseURL = baseUrl + '/api/users/login';
        var requestData = {
          "username": data.username,
          "password": data.password
        }
        var headers = {
          headers: {
            'Content-Type': 'application/json'
          }
        }
        $http.post(baseURL, requestData)
          .then(function(result) {
            $log.debug('Created object, ', result);
            promise.resolve(result);
          }, function(result) {
            $log.debug("Failed to create object:", result);
            promise.reject(result);
          });
        return promise.promise;
      },
      signup: function(data) {
        var promise = $q.defer();
        var baseURL = baseUrl + '/api/users/create';
        var requestData = {
          "username": data.username,
          "email": data.email,
          "password": data.password
        }
        var headers = {
          headers: {
            'Content-Type': 'application/json'
          }
        }
        $http.post(baseURL, requestData)
          .then(function(result) {
            $log.debug('Created object, ', result);
            promise.resolve(result);
          }, function(result) {
            $log.debug("Failed to create object:", result);
            promise.reject(result);
          });
        return promise.promise;
      },
      login: function(data) {
        var promise = $q.defer();
        var baseURL = baseUrl + '/api/users/login';
        var requestData = {
          "username": data.username,
          "password": data.password
        }
        var headers = {
          headers: {
            'Content-Type': 'application/json'
          }
        }
        $http.post(baseURL, requestData)
          .then(function(result) {
            $log.debug('Created object, ', result);
            promise.resolve(result);
          }, function(result) {
            $log.debug("Failed to create object:", result);
            promise.reject(result);
          });
        return promise.promise;
      },
      checkLogin: function(data) {
        console.log(localStorage.getItem('token'));
        var promise = $q.defer();
        var baseURL = baseUrl + '/api/users/checkUserSession';
        var requestData = {
          "userId": localStorage.getItem('userId')
        }
        var headers = {
          headers: {
            'Content-Type': 'application/json',
            'authorization': localStorage.getItem('token')
          }
        }
        $http.post(baseURL, requestData, headers)
          .then(function(result) {
            $log.debug('Created object, ', result);
            promise.resolve(result);
          }, function(result) {
            $log.debug("Failed to create object:", result);
            promise.reject(result);
          });
        return promise.promise;
      },
      getAllTodos: function() {
        var promise = $q.defer();
        var baseURL = baseUrl + '/api/todo';
        var requestData = {
          "userId": localStorage.getItem('userId'),
          "status": "all"
        }
        var headers = {
          headers: {
            'Content-Type': 'application/json',
            'authorization': localStorage.getItem('token')
          }
        }
        $http.post(baseURL, requestData, headers)
          .then(function(result) {
            $log.debug('Created object, ', result);
            promise.resolve(result);
          }, function(result) {
            $log.debug("Failed to create object:", result);
            promise.reject(result);
          });
        return promise.promise;
      },
      addTodos: function(data) {
        var promise = $q.defer();
        var baseURL = baseUrl + '/api/todo/add';
        var requestData = {
          "userId": localStorage.getItem('userId'),
          data: data
        }
        var headers = {
          headers: {
            'Content-Type': 'application/json',
            'authorization': localStorage.getItem('token')
          }
        }
        $http.post(baseURL, requestData, headers)
          .then(function(result) {
            $log.debug('Created object, ', result);
            promise.resolve(result);
          }, function(result) {
            $log.debug("Failed to create object:", result);
            promise.reject(result);
          });
        return promise.promise;
      },
      changeTodos: function(data) {
        var promise = $q.defer();
        var baseURL = baseUrl + '/api/todo/changeStatus';
        var requestData = {
          "userId": localStorage.getItem('userId'),
          todoId: data.todoId,
          status: data.status,
        }
        var headers = {
          headers: {
            'Content-Type': 'application/json',
            'authorization': localStorage.getItem('token')
          }
        }
        $http.post(baseURL, requestData, headers)
          .then(function(result) {
            $log.debug('Created object, ', result);
            promise.resolve(result);
          }, function(result) {
            $log.debug("Failed to create object:", result);
            promise.reject(result);
          });
        return promise.promise;
      }

    }
  }
})()

(function() {
  'use strict';

  angular
    .module('front')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, $q, $state, $scope, $cookies, webDevTec, toastr, GatewayService) {

    //state change function
    $scope.goTo = function(goTo) {
      $state.go(goTo, {
        userId: localStorage.getItem('userId')
      });
    };
    //Redirect if not logged in
    if (localStorage.getItem('userId') == null || localStorage.getItem('token') == null) {
      $scope.goTo('user');
    } else {
      GatewayService.checkLogin()
        .then(function(done) {
          console.log(done);
        })
        .catch(function(err) {
          $scope.goTo('user');
        })
    };

    $scope.obj = {};
    $scope.funcs = {};
    $scope.obj.addTodo = {}

    $scope.obj.filterSelection = "today";

    //add a todo
    $scope.funcs.addTodo = function() {
      $scope.obj.addTodoLoad = false;
      $scope.obj.addTodoError = false;
      $scope.obj.addTodoSuccess = false;
      if ($scope.obj.addTodo.content == null || $scope.obj.addTodo.dueDate == null) {
        console.log("please enter something");
        $scope.obj.addTodoError = true;
        $scope.obj.addTodoErrorMsg = "Please enter something";
      } else {
        $scope.obj.addTodoLoad = true;
        GatewayService.addTodos($scope.obj.addTodo)
          .then(function(res) {
            $timeout(function() {
              $scope.obj.addTodoLoad = false;
              $scope.obj.addTodoSuccess = true;
              $scope.obj.addTodoSuccessMsg = "TODO ADDEDD";
              $scope.funcs.getAllTodos()
                .then(function(todos) {
                  $scope.obj.todos = todos;
                  $state.reload();
                })
              $scope.obj.addTodo = {};
            }, 1000);
          })
          .catch(function(err) {
            $scope.obj.addTodoLoad = false;
            $scope.obj.addTodoError = true;
            $scope.obj.obj.addTodoErrorMsg = "ERROR ADDING TODO";
            console.log(err);
          })
      }
    };

    //Get all Todos
    $scope.funcs.getAllTodos = function() {
      var promise = $q.defer();
      GatewayService.getAllTodos()
        .then(function(todos) {
          $scope.obj.finalTodos = todos.data
          promise.resolve(todos.data);
        })
        .catch(function(err) {
          promise.reject(err)
        })
      return promise.promise;
    };

    //Load all todos to view
    $scope.funcs.loadAllTodos = function() {
      $scope.funcs.getAllTodos()
        .then(function(todos) {
          $scope.obj.todos = filterTodos(todos, $scope.obj.filterSelection);
        })
    }

    $scope.funcs.changeFilter = function(filter) {
      $scope.obj.filterSelection = filter
      $scope.obj.todos = filterTodos($scope.obj.finalTodos, filter);
    }

    //When a todo is completed
    $scope.funcs.changeStatus = function(todo, i) {
      $scope.obj.todos[i].completed = "1";
      $scope.obj.finalTodos[i].completed = "1";
      var obj = {
        todoId: todo._id,
        status: "1",
      }
      GatewayService.changeTodos(obj)
        .then(function(res) {
          console.log(res);
        })
    }

    function dateDiffInDays(a, b) {
      var _MS_PER_DAY = 1000 * 60 * 60 * 24;
      var utc1 = Date.UTC(a.getFullYear(), a.getMonth(), a.getDate());
      var utc2 = Date.UTC(b.getFullYear(), b.getMonth(), b.getDate());
      var difference = parseInt(Math.floor((utc1 - utc2) / _MS_PER_DAY));
      return difference;
    }
    var filterTodos = function(todos, filter) {
      var dateDifference = 0;
      if (filter == 'today') {
        var dateDifference = 0;
      } else if (filter == 'week') {
        var dateDifference = 7;
      } else if (filter == 'month') {
        var dateDifference = 30;
      } else {
        var dateDifference = 365;
      }
      return (todos.filter(function(t) {
        var diff = dateDiffInDays(new Date(t.dueDate), new Date());
        return diff <= dateDifference
      }))

    }

    // var sDate = new Date("2017-12-05T18:30:00.000Z");
    // console.log(dateDiffInDays(sDate, new Date()));
  }
})();

(function() {
  'use strict';

  angular
    .module('front')
    .controller('UserController', UserController);

  function UserController($state, $cookies, $scope, $timeout, webDevTec, toastr, GatewayService) {
    console.log("---");
    localStorage.removeItem("userId");
    localStorage.removeItem("token");
    $scope.obj = {};
    $scope.load = {};
    $scope.funcs = {};
    $scope.obj.input = {};
    $scope.obj.default = 'login'

    $scope.goTo = function(goTo) {
      console.log(localStorage.getItem('userId'));
      $state.go(goTo, {
        userId: localStorage.getItem('userId')
      });
    }
    $('#date').attr('data-uk-datepicker', '{format:"DD.MM.YYYY"}');
    $scope.funcs.signin = function() {
      $scope.obj.errLogin = false;
      GatewayService.login($scope.obj.input)
        .then(function(res) {
          localStorage.setItem("userId", res.data.userId);
          localStorage.setItem("token", res.data.token);
          $scope.goTo('home');
        })
        .catch(function(err) {
          $scope.obj.errLogin = true
        })
    }

    $scope.funcs.signup = function() {
      $scope.obj.errLogin = false;
      $scope.obj.errSignup = false;
      GatewayService.signup($scope.obj.input)
        .then(function(res) {
          $state.reload();
        })
        .catch(function(err) {
          $scope.obj.errSignup = true
        })
    }
  }
})()

